import React from "react"
import { Link } from "gatsby"
import autoBind from 'react-autobind'

import LinesEllipsis from 'react-lines-ellipsis'
import responsiveHOC from 'react-lines-ellipsis/lib/responsiveHOC'
import { Textfit } from 'react-textfit';

import "../styles/UpcomingListStyle.sass"

const ResponsiveEllipsis = responsiveHOC()(LinesEllipsis)

class HomeCarousel extends React.Component {
  constructor(){
    super()
  }

  render() {
    const props = this.props
    const data = props.data
    var Items = data.map((itemData) =>
      <li className="UpcomingListItem">
        <div className="UpcomingListItemBody">
          <img className="UpcomingListItemImage" src={itemData.image}/>
          <div className="UpcomingListItemText">
            <Textfit max={32} mode="single" className="UpcomingListItemName">{itemData.name}</Textfit>
            <h2 className="UpcomingListItemName-Mobile">{itemData.name}</h2>
            <Textfit max={14} mode="single" className="UpcomingListItemInfo">
              <span className="UpcomingListItemDate">{itemData.date}</span>&nbsp;-&nbsp;
              <span className="UpcomingListItemTime">{itemData.time}</span>
              <span className="UpcomingListItemInfoDivider"> | </span>
              <span className="UpcomingListItemPrice">{itemData.price}</span>
            </Textfit>
            <ResponsiveEllipsis className="UpcomingListItemDescription"
              text={itemData.description} maxLine='7' ellipsis='...' trimRight basedOn='words' component='p' />
          </div>
        </div>
      </li>
    )
    return (
      <ul id="UpcomingList">
        {Items}
      </ul>
    )
  }
}

export default HomeCarousel
